<template>
    <div class="article">
      <!--加载动画-->
      <div class="loading" v-if="isLoading">
        <img src="../assets/loading.gif">
      </div>
      <!--article内容-->
      <div v-else>
        <!--头部部分-->
        <div class="topic_header">
          <div class="topic_title">{{post.title}}</div>
          <ul>
            <li>• 发布与{{post.create_at | formatData}}</li>
            <li>• 作者:{{post.author.loginname}}</li>
            <li>• {{post.visit_count}}次浏览</li>
            <li>• 来自{{post | tabFormatter}}</li>
          </ul>
          <div v-html="post.content" class="topic_content"></div>
        </div>
        <!--回复部分-->
        <div id="reply">
          <div class="topbar">回复</div>
          <div v-for="(reply,index) in post.replies" class="replySec">
            <div class="replyUp">
              <router-link :to="{
                name:'user_info',
                params:{
                  name:reply.author.loginname
                }
              }">
                <img :src="reply.author.avatar_url">        <!--回复者的头像-->
              </router-link>
              <router-link :to="{
               name:'user_info',
                params:{
                  name:reply.author.loginname
                }
              }">
                <span>{{reply.author.loginname}}</span>     <!--回复者的名字-->
              </router-link>
              <span>{{reply.index +1}}楼</span>            <!--第几楼-->
              <span v-if="reply.ups.length>0">            <!--ups代表的是有没有顶-->
                  §{{reply.ups.length}}                      <!--这是顶的次数-->
              </span>
              <span v-else></span>
            </div>
            <!--评论的内容-->
            <p v-html="reply.content"  ></p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  // Article组件的API：https://cnodejs.org/api/v1/topic/   +  帖子ID（5c976feefd41137eb7660fca）
    export default {
        name: "Article",
        data(){
          return{
            isLoading : false,
            post:{}   //代表当前页面的内容
          }
        },
        methods:{
          getData(){
            this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
              .then((res=>{
                if(res.data.success === true){
                  this.isLoading = false;
                  this.post = res.data.data
                }
              }))
              .catch((err)=>{
                console.log(err)
              })
          }
        },
        beforeMount() {
          this.isLoading = true;
          this.getData()
        },
        watch:{
            '$route'(to,from){
                this.getData()
            }
        }
    }
</script>

<style >
  @import url('../assets/github.css');
  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  .article:not(:first-child) {
    margin-right: 340px;
    margin-top: 15px;
  }

  #reply, .topic_header {
    background-color: #fff;
  }

  #reply {
    margin-top: 15px;
  }

  #reply img {
    width: 30px;
    height: 30px;
    position: relative;
    bottom: -9px;
  }

  #reply a, #reply span {
    font-size: 13px;
    color: #666;
    text-decoration: none;
  }
  .replySec{
    border-bottom:1px solid #e5e5e5;
    padding:0 10px;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }

  .replyUp a:nth-of-type(2) {
    margin-left: 0px;
    display: inline-block;
  }

  .topic_header {
    padding: 10px;
  }

  .topic_title {
    font-size: 22px;
    font-weight: bold;
    padding-top: 8px;
    width: 75%;
  }

  .topic_header ul {
    list-style: none;
    padding: 0px 0px;
    margin: 6px 0px;
  }

  .topic_header li {
    display: inline-block;
    font-size: 12px;
    color: #838383;
  }
  .content{
    padding: 20px;
    background: #fff;
    white-space:normal;
  }
  .topic_content {
    /*border-top: 1px solid #e5e5e5;*/
    padding: 10px;
  }

  .markdown-text img {
    /*width: 50% !important;*/
    cursor: pointer;
  }
</style>
